import React, { useEffect, useState } from "react";
import { searchObject } from "./parse";

export const getStoreByRole = (storeRoles = [], limit = 1) => {
  return searchObject("Store", {
    ascending: "createdAt",
    role: {
      c: "_Role",
      f: "containedIn",
      v: storeRoles,
      t: "pointer",
    },
  });
};
