import React, { useEffect, useState } from "react";
import { Anchor } from "grommet";
import { getStoreByRole } from "../library/store";
import UserContext from "../context/UserContext";

const Component = () => {
  const _userContext = React.useContext(UserContext);

  const [stores, setStores] = useState(null);

  useEffect(() => {
    const storeRoles =
      (_userContext &&
        _userContext.roleObjects
          .filter((ro) => ro.get("name").includes("Store-"))
          .map((ro) => ro.id)) ||
      [];
    if (storeRoles && storeRoles.length) {
      getStoreByRole(storeRoles, 1).then(setStores);
    }
  }, [_userContext]);

  return (
    <>
      <p>Welcome to bidbid.live</p>
      <Anchor href="/setupstore">Setup</Anchor> a store to get started.
      {stores &&
        stores.map((store, i) => (
          <>
            <p key={i}>
              <Anchor href={`/store/${store.get("uniqueName")}`}>Upload</Anchor>{" "}
              products to {store.get("name")} store to start selling.
            </p>
          </>
        ))}
    </>
  );
};

export default Component;
